export default {
	components: {},
	props: {
		/**
		 * 输入类型
		 */
		type: String,
		/**
		 * 值
		 */
		value: String,
		/**
		 * 占位符
		 */
		placeholder: String,
		/**
		 * 是否显示清除按钮
		 */
		clearable: {
			type: [Boolean, String],
			default: false
		},
		/**
		 * 是否显示密码可见按钮
		 */
		displayable: {
			type: [Boolean, String],
			default: false
		},
		/**
		 * 自动获取焦点
		 */
		focus: {
			type: [Boolean, String],
			default: false
		}
	},
	model: {
		prop: 'value',
		event: 'input'
	},
	data() {
		return {
			/**
			 * 显示密码明文
			 */
			showPassword: false,
			/**
			 * 是否获取焦点
			 */
			isFocus: false
		};
	},
	computed: {
		inputType() {
			const type = this.type;
			return type === 'password' ? 'text' : type;
		},
		clearable_() {
			return String(this.clearable) !== 'false';
		},
		displayable_() {
			return String(this.displayable) !== 'false';
		},
		focus_() {
			return String(this.focus) !== 'false';
		}
	},
	methods: {
		clear() {
			this.$emit('input', '');
		},
		display() {
			this.showPassword = !this.showPassword;
		},
		onFocus() {
			this.isFocus = true;
		},
		onBlur() {
			this.$nextTick(() => {
				this.isFocus = false;
			});
		},
		onInput(e) {
			this.$emit('input', e.target.value);
		}
	}
};
